
body {
  padding: 2em 1em;
}
.flexWrapper {
  display: flex;
  justify-content: space-between;
  margin: 2em 0;

  flex-flow: row wrap;
}
.flexWrapper .blogLabel {
  flex-basis: 100%;
  font-weight: 700;
  font-size: 1.75em;
  line-height: 1.75;
  margin-bottom: .25em;
  margin-top: 1em;
}
.flexWrapper > * {
  flex-basis: 48%;
  /*height: 400px;*/
}
#summary, #content {
  height: 350px;
}
.title {
  text-align: center;
  font-weight: 600;
  border-bottom: 2px solid #3F51B5;
}




